.container {
  margin: 0 auto;
  width: 1314px;
  padding: 0px 20px;
  background-color: $white-color;
  font-family: Montserrat;
  font-weight: 400;
  font-style: normal;
}
@media screen and (min-width: 768px) {
  .container {
    width: 688px;
  }
}
@media screen and (min-width: 1366px) {
  .container {
    width: 1314px;
  }
}
